<template>
    <div>
        <h3>アプリ設定</h3>
        <v-row class="mx-4">
            <v-text-field label="API URL" v-model="settings.apiUrl">
            </v-text-field>
        </v-row>
        <v-row class="mx-4">
            <v-text-field label="AUTH TOKEN" v-model="settings.authToken">
            </v-text-field>
        </v-row>
        <v-row class="ma-4">
            <p>※設定はこのデバイスのみに保存されます。</p>
            <v-spacer />
            <v-btn color="primary" @click="onSave">
                保存
            </v-btn>
        </v-row>
    </div>
</template>

<script>
    export default {
        name: 'Settings',
        data() {
            return {
                settings: { ...this.$store.state.settings },
            }
        },
        methods: {
            onSave(){
                this.$store.dispatch('saveSettings', {
                    settings: this.settings,
                });
            },
        },
    }
</script>