<template>
  <div>
    <v-card>
      <v-card-title>
        <v-container>
          <v-row class="my-3">
            <v-btn color="primary" @click="onAuthCheck" :loading="is_loading">
              認証
            </v-btn>
          </v-row>
          <template v-if="is_auth">
            <v-row>
              <v-btn color="primary" @click="onFL003"
                    :loading="is_loading">
                FL003
              </v-btn>
            </v-row>
            <v-row>
              <v-data-table :headers="headers"
                            :items="items">
              </v-data-table>
            </v-row>
          </template>
        </v-container>
      </v-card-title>
    </v-card>
  </div>
</template>

<script>
  import gasApi from '../api/gasApi'
  import axios from "axios";

  export default {
    name: 'Home',
    mounted() {
      this.$nextTick(() => {
        // nextTickを使用してコンソールにログを出力します。
        console.log('hello');
        // リクエスト先のURLを変更する
        gasApi.setUrl('')
      });
    },
    data() {
      return {
        settings: { ...this.$store.state.settings },
        result: '',
        is_auth: false,
        is_loading: false,
        items: []
      }
    },
    computed: {
      headers(){
        return [
          { text: '時間', value: 'log_dttm' },
          { text: 'メッセージ', value: 'log_message' },
        ];
      },

    },
    methods: {
      async onAuthCheck(){
        try {
          this.is_loading = true;
          //リクエストに付加するヘッダーの定義
          const gasApi = axios.create({
            headers: { 
              'Accept': 'application/json',
              'content-type': 'application/x-www-form-urlencoded'
            }
          })
          gasApi.defaults.baseURL = this.settings.apiUrl;
          const data = {
            action: 'AUTH_CHECK',
            api_key: this.settings.authToken,
            params: {
            }
          }

          var params = new URLSearchParams();
          params.append('data', JSON.stringify(data));

          const result = await gasApi.post('', params);
          if(result.data.status == 'OK'){
            this.is_auth = true;
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.is_loading = false;
        }
      },
      async onFL003(){
        try {
          this.is_loading = true;
          //リクエストに付加するヘッダーの定義
          const gasApi = axios.create({
            headers: { 
              'Accept': 'application/json',
              'content-type': 'application/x-www-form-urlencoded'
            }
          })

          gasApi.defaults.baseURL = this.settings.apiUrl;
          const data = {
            method: 'POST',
            // authToken,
            action: 'FL003',
            api_key: this.settings.authToken,
            params: {
              a: 'bbcddd'
            }
          }

          var params = new URLSearchParams();
          params.append('data', JSON.stringify(data));

          const result = await gasApi.post('', params);

          this.result = result.data;
          this.items = result.data.log;
        } finally {
          this.is_loading = false;
        }
      },
    },
  }
</script>
